.countrie {
  width: 100vw;
  height: calc(100vh - 81px);
  z-index: 1;
}

.lightTheme {
  background-color: #fafafa;
}

.darkTheme {
  background-color: #212e37;
  color: #fff;
}

.infos {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 520px 1fr;
  grid-template-rows: 1fr 2fr 1fr;
}

.flag {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  overflow: hidden;
}

.flag img {
  width: 100%;
  height: 100%;
  background-position: center;
}

.countrieInfos {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-left: 250px;
}

.groupInfos01 {
  grid-column: 1 / 2;
}

.borderCountries {
  width: 100%;
  height: 100%;
  grid-column: 1 / 3;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.btn {
  display: flex;
  align-items: center;
}

.btnBack {
  width: 140px;
  height: 40px;
  border: 1px solid #e9e8e8;
  background-color: #fafafa;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.05);
  transition: all 0.4s;
  cursor: pointer;
}

.btnBack:hover {
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}

.darkBack {
  background-color: #354957;
  color: #fff;
  border: 1px solid #2c3d49;
}

.lightBack {
  border: 1px solid #e9e8e8;
}
